.reviews .slick-slider {
    position: relative;
}


.reviews .slick-slider .slick-arrow{
    position: absolute;
    top: 50%; //   
    transform: translateY(-50%);
    margin: -15px 0 0 0; //    
    z-index: 2; //   
    font-size: 0; //     
    width: 30px; //  
    height: 40px    ; //  
    cursor: pointer;
}

.reviews .slick-slider .slick-arrow.prev{
    left: -18px; //       
    background: url('../../media/icons/arrow-slider-left.svg') center / cover no-repeat;

    @media (min-width: $tablet-width) {
        left: -10px;
    }
}

.reviews .slick-slider .slick-arrow.next{
    right: -18px; //      
    background: url('../../media/icons/arrow-slider-right.svg') center / cover no-repeat;

    @media (min-width: $tablet-width) {
        right: -10px;
    }
}

.reviews__image-slider .slick-track {
    width: 100%;
    max-width: 100vw;
}